.tabs-list {
  padding: 0;
  list-style: none;
}

.tabs-list__item {
  width: 112px;
  border-right: 1px solid @grey-alpha-5;

  &:last-child {
    border: none;
  }

}

.tabs-list__link {
  display: block;
  padding: 15px 0;
  font-weight: 700;
  font-size: 11px;
  line-height: 17px;
  text-transform: uppercase;
  color: @dark;
  text-align: center;

  &:hover {
    color: @main-yellow;
  }
}

@media (max-width: @mobile-width-only) {
  .tabs-list {
    flex-wrap: wrap;
  }

  .tabs-list__item  {
    width: 100px;
    margin-bottom: 10px;
    border-right: none;
  }
}
