.button {
  display: inline-block;
  min-width: 186px;
  padding: 17px 58px;
  font-size: 11px;
  font-weight: bold;
  line-height: 19px;
  text-transform: uppercase;
  color: @white;
  border: 1px solid @main-yellow;
  border-radius: 5px;

  &--main {
    background-color: @main-yellow;

    &:hover {
      border: 1px solid @white-alpha-5;
      background-color: transparent;
    }
  }

  &--ghost {
    border: 1px solid @white-alpha-5;

    &:hover {
      border: 1px solid @main-yellow;
      background-color: @main-yellow;
    }
  }

  &--big {
    width: 100%;

    &:hover {
      border: 1px solid @main-yellow;
      background-color: transparent;
      color: @black;
    }
  }
}
