body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 11px;
  line-height: 18px;
  font-family: 'Open Sans', sans-serif;
  color: #ffffff;
  background: #ffffff;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
html {
  scroll-behavior: smooth;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
@font-face {
  font-family: "Banny";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../font/banny-boldround-ext-latin.woff2") format("woff2"), url("../font/banny-boldround-ext-latin.woff") format("woff");
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 1250px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}
.container--relative {
  position: relative;
}
@media (max-width: 1310px) {
  .container {
    width: 900px;
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media (max-width: 900px) {
  .container {
    width: 748px;
    padding-left: 10px;
    padding-right: 10px;
  }
}
@media (max-width: 767px) {
  .container {
    width: 100%;
