    padding-right: 10px;
  }
}
.page-header {
  position: relative;
  padding-bottom: 87px;
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url("../img/header-bg.jpg") center / cover;
}
.page-header::before,
.page-header::after {
  content: "";
  position: absolute;
  left: 50%;
  width: 1px;
  background-color: rgba(255, 255, 255, 0.2);
  transform: translate(-50%);
}
.page-header::before {
  top: 189px;
  bottom: 133px;
}
.page-header::after {
  top: 669px;
  bottom: 0;
}
.page-header__wrapper {
  position: relative;
}
.page-header__wrapper::before,
.page-header__wrapper::after {
  content: "";
  position: absolute;
  top: 93px;
  bottom: 0;
  width: 1px;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.2);
}
.page-header__wrapper::before {
  left: 200px;
}
.page-header__wrapper::after {
  right: 200px;
}
.page-header__logo {
  position: absolute;
  top: 23px;
  left: 50%;
  transform: translateX(-50%);
}
.page-header__nav {
  margin-bottom: 211px;
}
.page-header__main {
  display: flex;
  justify-content: space-between;
  margin-bottom: 84px;
}
.page-header__toggle-menu {
  position: relative;
  display: none;
  width: 35px;
  height: 24px;
  margin-left: auto;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 3;
  outline: none;
}
.page-header__toggle-menu--closed::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
}
.page-header__toggle-menu--opened {
  position: fixed;
  right: 21px;
}
.page-header__toggle-menu--opened::before,
.page-header__toggle-menu--opened::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  width: 35px;
  height: 2px;
  background-color: #000000;
}
.page-header__toggle-menu--opened::before {
  transform: rotate(45deg);
}
.page-header__toggle-menu--opened::after {
  transform: rotate(-45deg);
}
@media (max-width: 1310px) {
  .page-header__logo {
    width: 100px;
  }
  .page-header::before {
    top: 160px;
  }
}
@media (max-width: 900px) {
  .page-header__toggle-menu {
    display: block;
  }
  .page-header__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 15px;
  }
  .page-header__logo {
    position: static;
    width: 75px;
    transform: translate(0);
  }
  .page-header__logo img {
    width: 75px;
    height: 75px;
  }
  .page-header__wrapper::after,
  .page-header__wrapper::before,
  .page-header::before {
    display: none;
  }
  .page-header__nav {
    margin-bottom: 100px;
  }
}
@media (max-width: 767px) {
  .page-header__main {
    flex-direction: column;
    margin-bottom: 45px;
  }
  .page-header__nav {
    margin-bottom: 130px;
  }
  .page-header {
    padding-bottom: 45px;
  }
  .page-header::after,
  .page-header::before {
    display: none;
  }
  .page-header__logo {
    width: 65px;
    height: 65px;
  }
}
.main-nav {
  position: relative;
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav::before,
.main-nav::after {
  content: "";
  position: absolute;
  top: 93px;
  width: 494px;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.2);
}
.main-nav::before {
  left: 0;
}
.main-nav::after {
  right: 0;
}
.main-nav__item {
  width: 11%;
  text-align: center;
}
.main-nav__item:nth-child(8n + 4) {
  margin-right: 8%;
}
.main-nav__item:nth-child(8n + 5) {
  margin-left: 9%;
}
.main-nav__link {
  display: block;
  padding-top: 40px;
  padding-bottom: 37px;
  font-weight: bold;
  line-height: 17px;
  text-transform: uppercase;
  color: #ffffff;
}
.main-nav__link:hover {
  color: #E8C300;
}
@media (max-width: 1310px) {
  .main-nav::before,
  .main-nav::after {
    content: "";
    width: 360px;
  }
  .main-nav__item:nth-child(8n+5) {
    margin-left: 6%;
  }
  .main-nav__item:nth-child(8n+4) {
    margin-right: 6%;
  }
}
@media (max-width: 900px) {
  .main-nav::after,
  .main-nav::before {
    display: none;
  }
  .main-nav--closed {
    transform: translateX(200%);
  }
  .main-nav--opened {
    transform: translateX(0);
  }
  .main-nav__link:hover {
    color: #000000;
  }
  .main-nav {
    position: fixed;
    flex-direction: column;
    top: 0;
    bottom: 0;
    right: 0;
    width: 35%;
    padding-top: 65px;
    background-color: #E8C300;
    z-index: 1;
    overflow-y: auto;
    transition: 0.6s ease-in-out all;
  }
  .main-nav__item {
    width: 100%;
  }
  .main-nav__item:nth-child(8n+5),
  .main-nav__item:nth-child(8n+4) {
    margin-left: 0;
  }
  .main-nav__link {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .main-nav {
    width: 60%;
  }
}
