.features {
  position: relative;
  padding-top: 32px;
  padding-bottom: 40px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url("../img/specialtys-bg.jpg");
}
.features::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  width: 1px;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.2);
  transform: translate(-50%);
}
.features__info {
  margin: 0;
  margin-top: 37px;
  font-size: 11px;
  line-height: 11px;
  color: rgba(255, 255, 255, 0.5);
  text-align: center;
}
.features__info--bold {
  font-weight: 700;
}
.features__wrapper {
  position: relative;
}
.features__wrapper::before,
.features__wrapper::after {
  content: "";
  position: absolute;
  top: -8%;
  bottom: -7%;
  width: 1px;
  background-color: rgba(255, 255, 255, 0.2);
}
.features__wrapper::before {
  left: 200px;
}
.features__wrapper::after {
  right: 200px;
}
@media (max-width: 1310px) {
  .features__wrapper::after,
  .features__wrapper::before {
    display: none;
  }
}
