.main-nav {
  position: relative;
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;

  &::before, &::after {
    content: "";
    position: absolute;
    top: 93px;
    width: 494px;
    height: 1px;
    background-color: @white-alpha-2;
  }
  &::before {
    left: 0;
  }
  &::after {
    right: 0;
  }
}

.main-nav__item {
  width: 11%;
  text-align: center;

  &:nth-child(8n + 4) {
    margin-right: 8%;
  }

  &:nth-child(8n + 5) {
    margin-left: 9%;
  }
}

.main-nav__link {
  display: block;
  padding-top: 40px;
  padding-bottom: 37px;
  font-weight: bold;
  line-height: 17px;
  text-transform: uppercase;
  color: @white;

  &:hover {
    color: @main-yellow;
  }
}

@media (max-width: @desktop-width) {
  .main-nav {
    &::before, &::after {
      content: "";
      width: 360px;
    }
  }

  .main-nav__item:nth-child(8n+5) {
    margin-left: 6%;
  }

  .main-nav__item:nth-child(8n+4) {
    margin-right: 6%;
  }
}

@media (max-width: @tablet-width) {
  .main-nav::after, .main-nav::before {
    display: none;
  }

  .main-nav--closed {
    transform: translateX(200%);
  }

  .main-nav--opened  {
    transform: translateX(0);
  }

  .main-nav__link:hover {
    color: @black;
  }

  .main-nav {
    position: fixed;
    flex-direction: column;
    top: 0;
    bottom: 0;
    right: 0;
    width: 35%;
    padding-top: 65px;
    background-color: @main-yellow;
    z-index: 1;
    overflow-y: auto;
    transition: 0.6s ease-in-out all;
  }

  .main-nav__item {
    width: 100%;
  }

  .main-nav__item:nth-child(8n+5),
  .main-nav__item:nth-child(8n+4) {
    margin-left: 0;
  }

  .main-nav__link {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

@media (max-width: @mobile-width-only) {
  .main-nav {
    width: 60%;
  }
}
