.menu {
  padding-top: 70px;
  padding-bottom: 76px;
}

.menu__descr {
  margin-top: 32px;
  font-size: 17px;
  line-height: 26px;
  font-weight: 700;
  color: @black;
  text-align: center;
}

.menu__categories {
  display: flex;
  justify-content: center;
  margin-top: 44px;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
}

.menu__items-list {
  display: flex;
  flex-wrap: wrap;
  margin-top: 51px;
  margin-bottom: -30px;
  padding: 0;
  list-style: none;
  color: @dark;
}

.menu__items-list__item {
  margin-right: 42px;
  margin-bottom: 27px;
}

.items-list__product {
  display: flex;
  margin: 0;
}

.items-list__name,
.items-list__price {
  font-family: Banny, serif;
  font-weight: 700;
  font-size: 17px;
  line-height: 17px;
}

.items-list__name {
  font-size: 18px;
  margin-right: 17px;
}

.items-list__price {
  margin: 0;
}

.items-list__descr {
  margin: 0;
  font-size: 11px;
  line-height: 17px;
  color: @grey-alpha-5;
}

@media (max-width: @desktop-width) {
  .menu__items-list {
    justify-content: center;
  }
}

@media (max-width: @tablet-width) {
  .menu__items-list {
    flex-direction: column;
    align-items: center;
  }

  .menu__items-list__item {
    margin-right: 0;
  }

  .items-list__name,
  .items-list__price {
    font-size: 14px;
  }
}
