.features {
  position: relative;
  padding-top: 32px;
  padding-bottom: 40px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../img/specialtys-bg.jpg");

    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 50%;
      width: 1px;
      height: 100%;
      background-color: @white-alpha-2;
      transform: translate(-50%);
    }
}

.features__info {
  margin: 0;
  margin-top: 37px;
  font-size: 11px;
  line-height: 11px;
  color: @white-alpha-5;
  text-align: center;

  &--bold {
    font-weight: 700;
  }
}

.features__wrapper {
  position: relative;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: -8%;
    bottom: -7%;
    width: 1px;
    background-color: @white-alpha-2;
  }

  &::before {
    left: 200px;
  }

  &::after {
    right: 200px;
  }
}

@media (max-width: @desktop-width) {
  .features__wrapper::after, .features__wrapper::before {
    display: none;
  }
}
