.main-heading {
  position: relative;
  margin: 0;
  margin-bottom: 91px;
  font-family: Banny, serif;
  font-weight: bold;
  font-size: 90px;
  text-transform: uppercase;
  &--less {
    display: block;
    margin-bottom: 35px;
    font-family: Open Sans;
    font-weight: bold;
    font-size: 11px;
    line-height: 13px;
    letter-spacing: 0.5em;
    text-align: center;
    text-transform: uppercase;
  }

  &--promo {
    margin-bottom: 39px;
  }
}

@media (max-width: @desktop-width) {
  .main-heading {
    font-size: 72px;
  }
}

@media (max-width: @tablet-width) {
  .main-heading {
    font-size: 68px;
  }
}

@media (max-width: @mobile-width-only) {
  .main-heading {
    margin-bottom: 75px;
    font-size: 34px;
    text-align: center;
  }
  .main-heading--less {
    margin-bottom: 10px;
  }
}

