.book {
  padding-top: 69px;
  padding-bottom: 55px;
}

.book__col {
  width: 527px;
  margin: 0 auto;
  padding-top: 58px;
}

.book__form {
  margin-top: 31px;
}

.book__info {
  margin: 0;
  margin-top: 36px;
  font-size: 11px;
  line-height: 11px;
  color: @grey-alpha-5;
  text-align: center;

  &--bold {
    font-weight: 700;
  }
}

@media (max-width: @desktop-width) {
  .book__col {
    width: 450px;
    padding-top: 0;
  }
}

@media (max-width: @mobile-width-only) {
  .book__info {
    font-size: 13px;
    line-height: 1.2;
  }
}
