.form__row {
  display: flex;
  margin-bottom: 7px;
}

.form__filed {
  width: 100%;
  margin-right: 9px;
  padding-left: 19px;
  padding-top: 21px;
  padding-bottom: 19px;
  font-size: 13px;
  line-height: 18px;
  background-color: @grey;
  border-radius: 5px;
  border: none;
  color: @grey-alpha-5;

  &:last-child {
    margin-right: 0;
  }
}

  .form__filed::placeholder {
    font-family: inherit;
    font-size: inherit;
    color: @grey-alpha-5;
    opacity: 1;
  }

select {
  appearance: none;
  background-color: @grey;
  border: none;
  padding: 0 1em 0 0;
  margin: 0;
  width: 100%;
  font-family: inherit;
  font-size: inherit;
  cursor: inherit;
  line-height: inherit;
  color: inherit;
  z-index: 1;

  &::-ms-expand {
    display: none;
  }

  outline: none;
}

.form__filed--select {
  position: relative;
  color: @grey-alpha-5;
  cursor: pointer;

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 19px;
    width: 14px;
    height: 10px;
    background-image: url("../img/arow-botton-grey-icon.svg");
    background-repeat: no-repeat;
    transform: translateY(-50%);
  }
}

select:focus + .focus {
  position: absolute;
  top: -1px;
  left: -1px;
  right: -1px;
  bottom: -1px;
  border: 2px solid @black;
  border-radius: inherit;
}

@media (max-width: @mobile-width-only) {
  .form__row {
    flex-wrap: wrap;
    margin-bottom: 0;
  }

  .form__filed {
    margin-right: 0;
    margin-bottom: 10px;
  }
}
