
.promo {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 50%;
    width: 1px;
    background-color: @white-alpha-2;
    transform: translate(-50%);
  }
}

.promo__wrapper {
  position: relative;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: -8%;
    bottom: -19%;
    width: 1px;
    background-color: @white-alpha-2;
    overflow: hidden;
  }

  &::before {
    left: 200px;
  }

  &::after {
    right: 20px;
  }
}

.promo-slider__item {
  padding-top: 33px;
  padding-bottom: 93px;
  color: @white;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &--item-1 {
    background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../img/specialtys-bg.jpg");
  }

  &--item-2 {
    background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../img/header-bg.jpg");
  }

  &--item-3 {
    background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../img/specialtys-bg.jpg");
  }

  &--item-2,
  &--item-3 {
    display: none;
  }
}

.promo-slider__desription {
  width: 540px;
  margin-top: 110px;
  text-align: center;
}

.promo-slider__text--mian {
  margin-top: 23px;
  font-size: 17px;
  line-height: 26px;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.9);
}

.promo-slider__text {
  font-size: 13px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.9);
}

.promo-slider__text-line {
  display: inline-block;
  margin-bottom: 2px;
  padding-left: 11px;
  padding-right: 12px;
  background-color: rgba(255, 255, 255, 0.7);

  &:last-child {
    margin-bottom: 0;
  }
}

.promo-slider__radio-switcher {
  position: absolute;
  bottom: 35px;
  right: 50%;
  appearance: none;
  transform: translateX(44%);
}

.promo-slider__radio-switcher-item {
  position: relative;
  display: inline-block;
  width: 9px;
  height: 9px;
  margin-right: 19px;
  border-radius: 50%;
  border: none;
  background-color: transparent;
  cursor: pointer;
  fill: @white;
  fill-opacity: 0.5;
  outline: none;

  &:last-child {
    margin-right: 0;
  }

  &--active {
    fill-opacity: 1;
  }
}

@media (max-width: @desktop-width) {
  .promo-slider__desription {
    width: 430px;
    margin-top: 0;
  }
  .promo__wrapper::before, .promo__wrapper::after {
    display: none;
  }
}

@media (max-width: @desktop-width) {
  .promo-slider__desription {
    margin-left: auto;
    margin-right: auto;
  }
}

@media (max-width: @tablet-width) {
  .promo-slider__radio-switcher-item svg {
    width: 20px;
    height: 20px;
  }
}
