.social {
  margin: 0;
  padding: 0;
  list-style: none;
}

.social__item {
  width: 56px;
  height: 56px;
  margin-bottom: 19px;
}

.social__item:last-child {
  margin-bottom: 0;
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 1px solid @white-alpha-2;
  fill: @white;
}

.social__link:hover {
  fill: @main-yellow;
  border-color: @main-yellow;
}

@media (max-width: @mobile-width-only) {
  .social {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
  }

  .social__item,
  .banner__social li:first-child {
    margin: 0;
    margin-right: 25px;
  }

  .social__item {
    margin-right: 25px;
  }

  .social__item:last-child {
    margin-right: 0;
  }
}
