.timer {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 0;
  top: 112px;
  width: 240px;
  height: 64px;
  background: rgba(196, 196, 196, 0.3);
  font-size: 40px;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
}

.timer__value {
  display: block;
}

@media (max-width: @tablet-width) {
  .timer {
    left: 50%;
    transform: translateX(-50%);
  }
}
