.container {
  width: 1250px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;

  &--relative {
    position: relative;
  }
}

@media (max-width: @desktop-width) {
  .container {
    width: 900px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media (max-width: @tablet-width) {
  .container {
    width: 748px;
    padding-left: 10px;
    padding-right: 10px;
  }
}


@media (max-width: @mobile-width-only) {
  .container {
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
  }
}
