body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 11px;
  line-height: 18px;
  font-family: 'Open Sans', sans-serif;
  color: @white;
  background: @white;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

html {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}
