.button-down {
  position: relative;
  display: block;
  margin: 0 auto;
  padding: 0;
  width: 37px;
  height: 37px;
  border-radius: 50%;
  border: none;
  appearance: none;
  background-color: #ccc;
  background-color: @white;
  background-image: url(../img/arow-botton-icon.svg);
  background-repeat: no-repeat;
  background-position: center 12px;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 56px;
    height: 56px;
    border: 1px solid @white-alpha-2;
    border-radius: 50%;
    transform: translate(-50%, -50%);
  }
}
