.page-header {
  position: relative;
  padding-bottom: 87px;
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
  url("../img/header-bg.jpg") center/cover;

  &::before, &::after {
    content: "";
    position: absolute;
    left: 50%;
    width: 1px;
    background-color: @white-alpha-2;
    transform: translate(-50%);
  }

  &::before {
    top: 189px;
    bottom: 133px;
  }

  &::after {
    top: 669px;
    bottom: 0;
  }
}

.page-header__wrapper {
  position: relative;

  &::before,&::after {
    content: "";
    position: absolute;
    top: 93px;
    bottom: 0;
    width: 1px;
    height: 100%;
    background-color: @white-alpha-2;
  }

  &::before {
    left: 200px;
  }

  &::after {
    right: 200px;
  }
}

.page-header__logo {
  position: absolute;
  top: 23px;
  left: 50%;
  transform: translateX(-50%);
}

.page-header__nav {
  margin-bottom: 211px;
}

.page-header__main {
  display: flex;
  justify-content: space-between;
  margin-bottom: 84px;
}

.page-header__toggle-menu {
  position: relative;
  display: none;
  width: 35px;
  height: 24px;
  margin-left: auto;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 3;
  outline: none;
}

.page-header__toggle-menu--closed::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: @white;
  box-shadow: 0 8px 0 0 @white, 0 16px 0 0 @white;
}

.page-header__toggle-menu--opened {
  position: fixed;
  right: 21px;
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 8px;
    left: 0;
    width: 35px;
    height: 2px;
    background-color: @black;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

@media (max-width: @desktop-width) {
  .page-header__logo {
    width: 100px;
  }
  .page-header::before {
    top: 160px;
  }
}

@media (max-width: @tablet-width) {
  .page-header__toggle-menu {
    display: block;
  }
  .page-header__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 15px;
  }
  .page-header__logo {
    position: static;
    width: 75px;
    transform: translate(0);
  }
  .page-header__logo img {
    width: 75px;
    height: 75px;
  }
  .page-header__wrapper::after,
  .page-header__wrapper::before,
  .page-header::before {
    display: none;
  }
  .page-header__nav {
    margin-bottom: 100px;
  }
}

@media (max-width: @mobile-width-only) {
  .page-header__main {
    flex-direction: column;
    margin-bottom: 45px;
  }
  .page-header__nav {
    margin-bottom: 130px;
  }
  .page-header {
    padding-bottom: 45px;
  }
  .page-header::after,
  .page-header::before {
    display: none;
  }
  .page-header__logo {
    width: 65px;
    height: 65px;
  }
}
