.banner__info {
  width: 58px;
  margin: 0;
  font-weight: 700;
  line-height: 11px;
  transform: rotate(180deg);
  writing-mode: tb-rl;
}

.banner__actions {
  text-align: center;
}

.banner__actions .button:not(:last-child) {
  margin-right: 37px;
}

.banner__social li:first-child{
  margin-top: 13px;
}

@media (max-width: @mobile-width-only) {
  .banner__info {
    width: 100%;
    margin-bottom: 30px;
    transform: rotate(0);
    writing-mode: lr-tb;
    text-align: center;
  }

  .banner__actions {
    margin-bottom: 45px;
  }

  .banner__actions .button:not(:last-child) {
    margin-right: 0;
    margin-bottom: 15px;
  }
}
