.image-col {
  position: relative;
  width: 548px;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    width: 211px;
    height: 211px;
    border: 19px solid @main-yellow;
    border-radius: 5px;
    box-shadow: 0px 0px 93px @black-alpha-5;
    transform: translate(0, -50%);
  }

  &--right {

    &::before {
      right: 0;
    }
  }

  &--left {
    display: flex;

    &::before {
      left: 0;
    }

    .image-col__picture {
      margin-left: auto;
    }
  }

  &--promo {
    width: 565px;

    &::before {
      width: 232px;
      height: 245px;
    }
  }

  &--features {

    &::before {
      width: 232px;
      height: 232px;
    }
  }
}

.image-col__picture {
  border-radius: 5px;
  box-shadow: 0 0 47px @black-alpha-5;
}

.image-col__caption {
  position: absolute;
  display: flex;
  align-items: center;
  top: 0;
  bottom: 0;
  width: 75px;
  height: 100%;
  background-color: @black;
  font-family: Banny, serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 11px;
  text-indent: 19px;
  text-transform: uppercase;
  writing-mode: tb-rl;

  &--left {
    left: 0;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    transform: rotate(180deg);
  }

  &--right {
    right: 0;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
  }
}

@media (max-width: @desktop-width) {
  .image-col {
    width: 350px;

    &::before {
      display: none;
    }

    &--promo {
      margin-left: auto;
      margin-right: auto;
    }
  }

  .image-col__picture {
    width: 350px;
    height: 350px;
  }
}

@media (max-width: @tablet-width) {
  .image-col--book,
  .image-col--promo {
    display: none;
  }
}

@media (max-width: @mobile-width-only) {
  .features .image-col {
    margin-top: 20px;
  }
  .image-col--features {
    width: 300px;
  }
  .image-col__picture {
    width: 300px;
    height: 300px;
  }
  .image-col__caption--left {
    left: auto;
    right: 0;
    transform: rotate(0);
  }
}

