.line::before {
  content: "";
  position: absolute;
  top: 142px;
  left: 50%;
  width: 56px;
  height: 5px;
  transform: translateX(-50%);
  background-color: @main-yellow;
  border-radius: 47px;
}

.section-heading.line::before {
  top: 52px;
}


@media (max-width: @mobile-width-only) {
  .line::before,
  .section-heading.line::before {
    top: 120%;
  }
  .main-heading.line::before {
    top: 135%;
  }
  .section-heading.line::before {
    top: 110%;
  }
}
